<template>
  <div class="fixed top-0 left-0 h-full w-full bg-white-80 flex items-center justify-center z-20">
    <div class="flex">
      <div>{{ message }}</div>
      <div class="w-0">{{ dots() }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nDots: 3,
    };
  },
  props: {
    message: {
      type: String,
      default: 'Loading',
    },
  },
  methods: {
    updateDots() {
      this.nDots = (this.nDots + 1) % 4;
    },
    dots() {
      return '.'.repeat(this.nDots);
    },
    startInterval() {
      setInterval(() => { this.updateDots(); }, 1000);
    },
  },
  mounted() {
    this.startInterval();
  },
};
</script>
