<template>
  <div class='relative bg-white-90 rounded z-20 m-1 p-2' data-html2canvas-ignore>
    <div
      class="p-2 transition-all transition-500"
      :class="collapsed ? 'w-10vw' : 'w-20vw'">
      <div>
        <h3 class="text-sm text-center border-b border-b-grey-500 mb-2">
          <TransitionText :text=name />
        </h3>
        <div class="flex justify-between transition-all transition-2s" :class="zone ? 'max-h-200' : 'max-h-0'">
          <h4 class="text-xs"><TransitionText :text=variable /></h4>
          <h4 class="text-xs"><TransitionText :text="number | formatNumber" /></h4>
        </div>
      </div>
      <div
        class="transition-all transition-500 overflow-hidden"
        :class="collapsed ? 'max-h-0' : 'max-h-x'">
        <simplebar class="max-h-x pr-3 border-t mt-1 border-t-grey-500" data-simplebar-auto-hide="false">
          <div class="flex justify-between text-xs max-h-x">
            <div class="mr-4">
              <div v-for="property in properties" :key="property.name" class="mt-1">
                <TransitionText :text=property.name />
              </div>
            </div>
            <div>
              <div v-for="property in properties" :key="property.name" class="mt-1 text-right">
                <TransitionText v-if="property.value" :text="property.value | formatNumber" />
                <span v-else><strong>no info</strong></span>
              </div>
            </div>
          </div>
        </simplebar>
      </div>
      <!-- This mess down here is the little pink corner -->
      <div
        v-on:click=uncollapse()
        class="absolute bottom-0 left-0 rounded-bl border-l-2 border-b-2 w-8 h-8 transition-all transition-50
          border-pink-500 cursor-pointer
          hover:border-pink-400 hover:border-b-4 hover:border-l-4">
      </div>
    </div>
  </div>
</template>

<script>
import simplebar from 'simplebar-vue';
import 'simplebar/dist/simplebar.min.css';
import TransitionText from './TransitionText.vue';

export default {
  data() {
    return {
      collapsed: true,
    };
  },
  props: {
    zone: {
      type: Object,
    },
  },
  components: {
    TransitionText,
    simplebar,
  },
  computed: {
    name() {
      return (this.zone && this.zone.name) || 'Click a zone';
    },
    variable() {
      return (this.zone && this.zone.variable) || '';
    },
    number() {
      return (this.zone && this.zone.number) || '';
    },
    properties() {
      const result = [];
      if (this.zone) {
        const propFields = ['name', 'variable', 'id', 'number', this.zone.variable];
        Object.keys(this.zone).forEach((key) => {
          if (!(propFields.find(field => key === field))) {
            result.push({
              name: key,
              value: this.zone[key],
            });
          }
        });
      }
      return result;
    },
  },
  methods: {
    uncollapse() {
      this.collapsed = !this.collapsed;
    },
  },
};
</script>

<style lang="stylus">
.max-h-0
  max-height 0px;

.max-h-200
  max-height 200px;

.max-h-x
  max-height 30vh

.w-10vw
  width 15vw

.w-20vw
  width 20vw
</style>
