<template>
  <div
    :style=divStyle
    class="bg-white-80 z-20 rounded m-1 transition-all transition-500 overflow-hidden">
    <svg
      :style=svgStyle
      class=''
      id="legendSvg">
    </svg>
    <canvas
      :style=svgStyle
      id="tempLegendCanvas"
      class="hidden">
    </canvas>
  </div>
</template>

<script>
import * as d3Legend from 'd3-svg-legend';
import * as d3 from 'd3';

export default {
  props: {
    scale: {
      type: Function,
      default: null,
    },
    title: {
      type: String,
      default: 'Legend',
    },
  },
  data() {
    return {
      legend: null,
      size: {
        width: 0,
        height: 0,
        hPadding: 10,
        vPadding: 10,
      },
      formatter: d3.format(',.4f'),
    };
  },
  computed: {
    divStyle() {
      const totalHPadding = this.size.hPadding * 2;
      const totalVPadding = this.size.vPadding * 2;
      const padding = `${this.size.vPadding}px ${this.size.hPadding}px`;
      return {
        width: `${this.size.width + totalHPadding}px`,
        height: `${this.size.height + totalVPadding}px`,
        padding,
      };
    },
    svgStyle() {
      return {
        width: `${this.size.width}px`,
        height: `${this.size.height}px`,
        'font-size': '14px',
      };
    },
  },
  methods: {
    initLegend() {
      if (this.scale) {
        this.legend = d3Legend.legendColor()
          .scale(this.scale)
          .titleWidth(120)
          .labelFormat(this.formatter)
          .title(this.title);
        d3.selectAll('#legendSvg > *').remove();
        const svg = d3.select('#legendSvg');
        const g = svg.append('g')
          .attr('class', 'legendQuant')
          .attr('transform', 'translate(0,16)');
        svg.select('.legendQuant')
          .call(this.legend);
        const bbox = g.node().getBBox();
        this.setSize(bbox.width, bbox.height);
      }
    },
    setSize(width, height) {
      this.size.width = width;
      this.size.height = height;
    },
  },
  mounted() {
    this.initLegend();
  },
  watch: {
    scale() {
      this.initLegend();
    },
  },
};
</script>
