<template>
  <div
    :class="collapsed ? 'max-w-0' : 'max-w-full'"
    class="relative h-full transition-all transition-500">
    <svg id="collapse-container" class="absolute z-20" height="60" width="60">
      <g
        id="collapse-button"
        :class="[{'collapse-button-collapsed': btnCollapsed, 'collapse-button-hover cursor-pointer': btnHover}]"
        @click=toggleCollapse()
        @mouseover=toggleCollapseHover()
        @mouseout=toggleCollapseHover()>
        <path class="transition-all transition-500"
              d="M0,0
                Q 40,30 0,60"
              id="outside-thing"/>
        <path class="transition-all transition-500"
              d="M6,25
                L10,30
                L6,35"
              id="little-arrow"/>
      </g>
    </svg>
    <div
      :class="{'border-r border-gray-600': btnHover}"
      class="h-full overflow-hidden w-250px p-2 transition-all transition-500">
      <div class="w-full flex flex-col justify-center p-3 mb-2 rounded border border-gray-300">
        <h4 class="text-base text-center border-b border-pink-200 pb-2 mb-2">
          Data
        </h4>
        <div class="mb-2">
          <span class="text-sm text-gray-700">Variable</span>
          <v-select
            :options="variables.options"
            v-model="variables.selected"
            @input="changeVariable"
            :clearable=false
            class="w-full text-sm">
          </v-select>
        </div>
        <div class="mb-2">
          <span class="text-sm text-gray-700">Level</span>
          <v-select
            :options="levels.options"
            v-model="levels.selected"
            @input="changeLevel"
            :reduce="option => option.value"
            label="text"
            :clearable=false
            class="w-full text-sm">
          </v-select>
        </div>
      </div>
      <div class="w-full flex flex-col justify-center p-3 mb-2 rounded border border-gray-300">
        <h4 class="text-base text-center border-b border-pink-200 pb-2 mb-2">
          Map
        </h4>
        <div class="mb-2">
          <span class="text-sm text-gray-700">Color scale</span>
          <v-select
            :options="scaleColor.options"
            v-model="scaleColor.selected"
            :reduce="option => option.value"
            label="text"
            @input="changeColor"
            :clearable=false
            class="w-full text-sm">
          </v-select>
        </div>
        <div class="mb-2">
          <span class="text-sm text-gray-700">Colors (categories)</span>
          <v-select
            :options="nColorCategories.options"
            v-model="nColorCategories.selected"
            @input="changeNColorCategories"
            :clearable=false
            class="w-full text-sm">
          </v-select>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from '../event-bus';

export default {
  name: 'LeftPanel',
  data() {
    return {
      collapsed: false,
      btnCollapsed: false,
      btnHover: false,
      scaleColor: {
        selected: 'orange-red',
        options: [
          { text: 'Blue', value: 'blue' },
          { text: 'Green', value: 'green' },
          { text: 'Grey', value: 'grey' },
          { text: 'Orange', value: 'orange' },
          { text: 'Purple', value: 'purple' },
          { text: 'Red', value: 'red' },
          { text: 'Blue - green', value: 'blue-green' },
          { text: 'Blue - purple', value: 'blue-purple' },
          { text: 'Green - blue', value: 'green-blue' },
          { text: 'Orange - red', value: 'orange-red' },
          { text: 'Purple - blue - green', value: 'purple-blue-green' },
          { text: 'Purple - blue', value: 'purple-blue' },
          { text: 'Purple - red', value: 'purple-red' },
          { text: 'Red - purple', value: 'red-purple' },
          { text: 'Yellow - green - blue', value: 'yellow-green-blue' },
          { text: 'Yellow - green', value: 'yellow-green' },
          { text: 'Yellow - orange - brown', value: 'yellow-orange-brown' },
          { text: 'Yellow - orange - red', value: 'yellow-orange-red' },
        ],
      },
      variables: {
        selected: null,
        options: [],
      },
      nColorCategories: {
        selected: 9,
        options: [3, 4, 5, 6, 7, 8, 9],
      },
      levels: {
        selected: this.$route.params.level,
        options: [
          { text: 'region', value: 'region' },
          { text: 'country', value: 'country' },
        ],
      },
    };
  },
  methods: {
    changeColor() {
      EventBus.$emit('changeColor', this.scaleColor.selected);
    },
    changeVariable() {
      EventBus.$emit('changeVariable', this.variables.selected);
    },
    changeNColorCategories() {
      EventBus.$emit('changeNColorCategories', this.nColorCategories.selected);
    },
    changeLevel() {
      this.$router.push(
        {
          name: 'map',
          params: {
            level: this.levels.selected,
          },
        },
      );
    },
    loadData(data) {
      this.variables.options = data.attributes;
      this.variables.selected = data.density_type;
    },
    setupEvents() {
      const that = this;
      EventBus.$on('loaded', that.loadData);
    },
    toggleCollapse() {
      this.collapsed = !this.collapsed;
      this.btnCollapsed = this.collapsed;
    },
    toggleCollapseHover() {
      this.btnHover = !this.btnHover;
    },
  },
  mounted() {
    this.setupEvents();
  },
};
</script>

<style lang="stylus">
  .w-250px
    width 250px

  .max-w-0
    max-width 0

  .max-w-full
    max-width 250px

  #collapse-container
    top calc(50% - 30px)
    right -59px

  #collapse-button
    fill white

  .collapse-button-hover
    #outside-thing
      stroke gray
      stroke-width 1px

  #little-arrow
    stroke-width 2px
    stroke black
    fill none
    transform-origin 13% 50%

  .collapse-button-collapsed
    #little-arrow
      transform rotate(-180deg)
</style>
