<template>
  <div id="map-container" class="relative flex-grow h-full">
    <div id="map" class="relative w-full h-full z-10"></div>
    <LoadingModal v-if="loading" :message="loadingModal.message" />
    <div class="absolute top-0 right-0 m-3 z-20 flex items-end justify-center flex-col">
      <MapLegend v-if="!loading" :scale="colorScale" :title="variable.longName" />
      <DetailPanel v-if="!loading" :zone=detailPanelContent />
    </div>
  </div>
</template>

<script>
import * as L from 'leaflet';
import 'leaflet.vectorgrid';
import axios from 'axios';
import * as d3Scale from 'd3-scale';
import * as d3Color from 'd3-scale-chromatic';

import 'leaflet/dist/leaflet.css';

import { EventBus } from '../event-bus';

import LoadingModal from './LoadingModal.vue';
import MapLegend from './MapLegend.vue';
import DetailPanel from './DetailPanel.vue';

const colorOptions = {
  blue: d3Color.schemeBlues,
  green: d3Color.schemeGreens,
  grey: d3Color.schemeGreys,
  orange: d3Color.schemeOranges,
  purple: d3Color.schemePurples,
  red: d3Color.schemeReds,
  'blue-green': d3Color.schemeBuGn,
  'blue-purple': d3Color.schemeBuPu,
  'green-blue': d3Color.schemeGnBu,
  'orange-red': d3Color.schemeOrRd,
  'purple-blue-green': d3Color.schemePuBuGn,
  'purple-blue': d3Color.schemePuBu,
  'purple-red': d3Color.schemePuRd,
  'red-purple': d3Color.schemeRdPu,
  'yellow-green-blue': d3Color.schemeYlGnBu,
  'yellow-green': d3Color.schemeYlGn,
  'yellow-orange-brown': d3Color.schemeYlOrBr,
  'yellow-orange-red': d3Color.schemeYlOrRd,
};

function initMap() {
  this.map = L.map('map', {
    renderer: L.canvas(),
    minZoom: 3,
    maxZoom: 16,
  }).setView([52, 20], 4);
  L.control.scale().addTo(this.map);
  //  this.tileLayer = L.tileLayer('http://localhost:8081/styles/klokantech-basic/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('http://a.tile.stamen.com/toner/{z}/{x}/{y}.png');
  this.tileLayer = L.tileLayer('https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_all/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('https://maps.wikimedia.org/osm-intl/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('http://vre.hcmut.edu.vn/styles/klokantech-basic/{z}/{x}/{y}.png');
  //  this.tileLayer = L.tileLayer('http://217.182.134.100:3003/styles/custom/{z}/{x}/{y}.png');
  this.tileLayer.addTo(this.map);
  this.selectedLevel = this.$route.params.level;
}

function buildScale() {
  const densities = this.geoJson.features.map(
    element => this.calculateValueOfInterest(element.properties),
  );
  this.colorScale = d3Scale.scaleQuantile()
    .domain(densities)
    .range(colorOptions[this.colorScheme][this.nColorCategories]);
}

function styleFunction() {
  const defaultStyle = this.defaultFeatureStyle;
  const colorFn = this.fillColor;
  return function style(feature) {
    return {
      ...defaultStyle,
      fillColor: colorFn(this.calculateValueOfInterest(feature.properties)),
    };
  };
}

function vectorTileLayerStyleFunction() {
  const that = this;
  return function style(properties, zoom) {
    const borderStyle = zoom > 6 ? that.defaultFeatureBorderStyle : that.noBorderStyle;
    return {
      ...that.defaultFeatureStyle,
      ...borderStyle,
      fillColor: that.fillColor(that.calculateValueOfInterest(properties)),
    };
  };
}

function createVectorGrid(geoJson) {
  const vg = L.vectorGrid.slicer(
    geoJson,
    {
      rendererFactory: L.canvas.tile,
      interactive: true,
      getFeatureId(feature) {
        return feature.properties.id;
      },
      vectorTileLayerStyles: {
        sliced: this.vectorTileLayerStyleFunction,
      },
      maxZoom: 24,
      tolerance: 2,
    },
  ).on({
    mouseover: this.handleVectorTileMouseover,
    mouseout: this.handleVectorTileMouseout,
    click: this.handleVectorTileClick,
  });
  return vg;
}

function clearHighlight(key) {
  if (this.highlights[key]) {
    this.vectorGrid.resetFeatureStyle(this.highlights[key]);
  }
  this.highlights[key] = null;
}

function clearMouseover() {
  if (this.highlights.mouseOver !== this.highlights.clicked) {
    this.clearHighlight('mouseOver');
  }
}

function highlightVectorGridFeature(properties, weight, fillOpacity, color) {
  if (this.highlights.clicked !== properties.id) {
    this.vectorGrid.setFeatureStyle(
      properties.id,
      {
        weight: weight || 1,
        fillOpacity: fillOpacity || 0.7,
        color: color || 'white',
        fill: true,
        stroke: true,
        fillColor: this.fillColor(this.calculateValueOfInterest(properties)),
      },
    );
  }
}

function handleVectorTileMouseover(event) {
  const { properties } = event.layer;
  this.clearMouseover();
  this.highlightVectorGridFeature(properties);
  this.highlights.mouseOver = properties.id;
  L.popup({
    className: 'custom-popup',
    autoPan: false,
    closeButton: false,
  }).setLatLng(event.latlng)
    .setContent(properties.name)
    .openOn(this.map);
}

function handleVectorTileMouseout(event) {
  this.clearMouseover();
}

function handleVectorTileClick(event) {
  const { properties } = event.layer;
  if (properties.id !== this.highlights.clicked) {
    this.clearClicked();
    this.highlightVectorGridFeature(properties, 3, 0.9);
    this.highlights.clicked = properties.id;
    this.selectedZone = properties;
  } else {
    this.clearClicked();
  }
}

function clearClicked() {
  this.clearHighlight('clicked');
}

function findCorners(geoJson) {
  const stack = geoJson.features.map(feature => feature.geometry.coordinates);
  const limits = {
    top: Number.NEGATIVE_INFINITY,
    right: Number.NEGATIVE_INFINITY,
    bottom: Number.POSITIVE_INFINITY,
    left: Number.POSITIVE_INFINITY,
  };
  let current = {};
  while (stack.length > 0) {
    current = stack.pop();
    if (Array.isArray(current[0])) {
      stack.push(...current);
    } else {
      const [lat, lng] = current;
      if (lat < limits.bottom) {
        limits.bottom = lat;
      } else {
        limits.top = Math.max(limits.top, lat);
      }
      if (lng < limits.left) {
        limits.left = lng;
      } else {
        limits.right = Math.max(limits.right, lng);
      }
    }
  }
  this.map.fitBounds([
    [limits.left, limits.top],
    [limits.right, limits.bottom],
  ]);
}

function loadWithData(geojson) {
  this.geoJson = geojson;
  this.loadingModal.message = 'Finding corners';
  this.findCorners(geojson);
  this.loadingModal.message = 'Building scale'; // longName, key
  this.variable.key = this.metadata.density_type;
  this.variable.longName = this.metadata.label;
  this.buildScale();
  this.loadingModal.message = 'Creating vector grid';
  this.vectorGrid = this.createVectorGrid(geojson);
  this.vectorGrid.on('load', this.hideLoading);
  this.loadingModal.message = 'Loading options';
  EventBus.$emit('loaded', this.metadata);
  this.loadingModal.message = 'Adding to map';
  this.vectorGrid.addTo(this.map);
}

function augmentGeoJson(geojson, extraData) {
  const attributesToAdd = this.metadata.attributes;
  const featuresWithData = [];
  geojson.features.forEach((feature, index) => {
    const { properties } = feature;
    const extra = extraData.data.find(item => item.sourceid === properties.sourceid);
    if (extra) {
      attributesToAdd.forEach((attribute) => {
        properties[attribute] = extra[attribute];
      });
      properties.id = (index + 1);
      featuresWithData.push(feature);
    }
  });
  geojson.features = featuresWithData;
}

async function initVectorTiles() {
  this.loading = true;
  this.loadingModal.message = 'Downloading results';
  const results = await axios.get(`${this.serviceBaseURL}/${this.resultResources[this.selectedLevel]}`);
  this.metadata = results.data.schema.metadata;
  this.loadingModal.message = 'Downloading base map';
  const baseMap = await axios.get(`${this.serviceBaseURL}/${this.baseMapResources[this.metadata.level]}`);
  this.loadingModal.message = 'Combining results';
  this.augmentGeoJson(baseMap.data, results.data);
  this.loadWithData(baseMap.data);
}

function hideLoading() {
  this.loading = false;
}

function fillColor(value) {
  // console.log('value', value);
  const result = this.colorScale(value);
  // console.log('result', result);
  return result;
}

function calculateValueOfInterest(properties) {
  return properties[this.variable.key];
}

function setupEvents() {
  const that = this;
  EventBus.$on('changeColor', (newColor) => {
    that.colorScheme = newColor;
    that.buildScale();
    that.vectorGrid.redraw();
  });
  EventBus.$on('changeVariable', (newVariable) => {
    that.variable.key = newVariable;
    that.variable.longName = newVariable;
    that.buildScale();
    that.vectorGrid.redraw();
  });
  EventBus.$on('changeNColorCategories', (newN) => {
    that.nColorCategories = newN;
    that.buildScale();
    that.vectorGrid.redraw();
  });
}

function detailPanelContent() {
  if (!this.selectedZone) return null;
  return {
    variable: this.variable.key,
    number: this.calculateValueOfInterest(this.selectedZone),
    ...this.selectedZone,
  };
}

export default {
  name: 'Map',
  components: {
    LoadingModal,
    MapLegend,
    DetailPanel,
  },
  data() {
    return {
      serviceBaseURL: 'maps/public',
      selectedLevel: this.$route.params.level,
      resultResources: {
        region: 'geo_regions.json',
        country: 'geo_countries.json',
      },
      baseMapResources: {
        country: '01_countries.json',
        region: '02_regions.json',
      },
      metadata: { },
      map: null,
      tileLayer: null,
      vectorGrid: null,
      currentGeoJson: null,
      colorScheme: 'orange-red',
      colorScale: null, // Will be overridden when map loads
      nColorCategories: 9,
      variable: {
        key: null,
        longName: null,
      },
      defaultFeatureBorderStyle: {
        weight: 1,
        opacity: 1,
        color: 'white',
      },
      noBorderStyle: {
        weight: 0.5,
        opacity: 0.8,
        color: 'grey',
      },
      defaultFeatureStyle: {
        fill: true,
        fillOpacity: 0.7,
      },
      loading: false,
      loadingModal: {
        message: 'Loading',
      },
      selectedZone: null,
      highlights: {
        mouseOver: null,
        clicked: null,
      },
    };
  },
  mounted() {
    this.initMap();
    this.initVectorTiles();
    this.setupEvents();
  },
  methods: {
    initMap,
    augmentGeoJson,
    initVectorTiles,
    createVectorGrid,
    fillColor,
    clearMouseover,
    clearClicked,
    highlightVectorGridFeature,
    handleVectorTileMouseover,
    handleVectorTileMouseout,
    handleVectorTileClick,
    hideLoading,
    buildScale,
    calculateValueOfInterest,
    setupEvents,
    clearHighlight,
    loadWithData,
    findCorners,
  },
  computed: {
    styleFunction,
    vectorTileLayerStyleFunction,
    detailPanelContent,
  },
  watch: {
    $route(to) {
      this.selectedLevel = to.params.level;
      this.vectorGrid.removeFrom(this.map);
      this.vectorGrid = null;
      this.initVectorTiles();
    },
  },
};
</script>
