import Vue from 'vue';
import * as d3 from 'd3';

const integerFormatter = d3.format(',');
const decimalFormatter = d3.format(',.4f');

Vue.filter('formatNumber', (input) => {
  if (!input) {
    return '';
  }
  if (!Number.isNaN(parseFloat(input))) {
    return input % 1 ? decimalFormatter(input) : integerFormatter(input);
  }
  return input;
});
