<template>
  <nav id="navbar" class="flex items-center justify-between flex-wrap text-white bg-gray-700 pr-4 pl-4 border-b-4 border-pink-500">
    <div class="flex h-full items-center mr-4">
      <div class="h-full p-2">
        <img class="h-full mr-2" src="../assets/logo.png" alt="">
      </div>
      <span class="font-semibold text-xl tracking-tight">
        Spatial exploration
      </span>
    </div>
    <div class="h-full flex flex-grow items-center justify-between">
      <div class="text-sm text-purple-200">
        <!-- <a href="#" class="hover:text-white mr-4">Docs</a>
        <a href="#" class="hover:text-white mr-4">Examples</a>
        <a href="#" class="hover:text-white mr-4">Blog</a> -->
      </div>
      <div>
        <button
          @click=downloadMap()
          class="text-sm px-4 py-1 border rounded hover:bg-white hover:text-gray-700 hover:border-transparent">
          Download
        </button>
      </div>
    </div>
  </nav>
</template>

<script>
import html2canvas from 'html2canvas';
import canvg from 'canvg';
import * as FileSaver from 'file-saver';

export default {
  name: 'TopNav',
  methods: {
    async downloadMap() {
      const tempLegendCanvas = document.querySelector('#tempLegendCanvas');
      const svgLegend = document.querySelector('#legendSvg');
      const xml = (new XMLSerializer()).serializeToString(svgLegend);
      tempLegendCanvas.classList.remove('hidden');
      svgLegend.classList.add('hidden');
      canvg(tempLegendCanvas, xml);
      const canvasRenderParams = {
        allowTainted: true,
        useCORS: true,
        foreignObjectRendering: true,
        ignoreElements: element => element.classList.contains('leaflet-control-zoom'),
      };
      const canvas = await html2canvas(document.querySelector('#map-container'), canvasRenderParams);
      canvas.toBlob((blob) => {
        FileSaver.saveAs(blob, 'map.png');
        svgLegend.classList.remove('hidden');
        tempLegendCanvas.classList.add('hidden');
        tempLegendCanvas.getContext('2d').clearRect(0, 0, tempLegendCanvas.width, tempLegendCanvas.height);
      });
    },
  },
};
</script>

<style lang="stylus">
  #navbar
    height 48px
</style>
